.ul-none {
    list-style: none;
    padding-left: 0;
    margin-bottom: 0;
}

.max-width-100 {
    max-width: 100%;
}


/* display */
.d-flex {
    display: flex;
}


/*  justify-content */
.j-content-between {
    justify-content: space-between;
}
.j-content-around {
    justify-content: space-around;
}
.j-content-center {
    justify-content: center;
}
.j-content-start {
    justify-content: start;;
}
.j-content-end {
    justify-content: end;
}


/* align-items */
.a-item-baseline {
    align-items: baseline;
}
.a-item-flex-start {
    align-items: flex-start;
}
.a-item-flex-end {
    align-items: flex-end;
}
.a-item-center {
    align-items: center;
}


/* flex-wrap */
.flex-wrap {
    flex-wrap: wrap;
}

.flex-nowrap {
    flex-wrap: nowrap;
}

/* margin */
.ml-auto {
    margin-left: auto;
}
.mr-auto {
    margin-right: auto;
}
.m-auto {
    margin-left: auto;
    margin-right: auto;
}